@mixin d {
  @media only screen and (max-width: "1120px") {
    @content; } }
@mixin t {
  @media only screen and (max-width: "1023px") {
    @content; } }
@mixin m {
  @media only screen and (max-width: "767px") {
    @content; } }
@mixin s {
  @media only screen and (max-width: "474px") {
    @content; } }

@include font(ProximaNova-Regular, ProximaNova-Regular);
@mixin f {
  font-family: 'ProximaNova-Regular', serif; }
@include font(ProximaNova-Bold, ProximaNova-Bold);
@mixin fb {
  font-family: 'ProximaNova-Bold', serif; }
@include font(ProximaNova-SemiBold, ProximaNova-SemiBold);
@mixin fs {
  font-family: 'ProximaNova-SemiBold', serif; }
@include font(ProximaNova-Light, ProximaNova-Light);
@mixin fl {
  font-family: 'ProximaNova-Light', serif; }

@mixin bs {
  box-shadow: 0 2px 5px rgba(black,.4); }

$green: #2ABFBF;
$border: #cecece;
$bg: #F5F7F9;
$cl: #6F7C82;

body {
  @include f;
  font-size: 16px;
  color: $cl;
  line-height: 1.5;
  &.no-scroll {
    overflow: hidden; } }
button,
input,
textarea,
select {
  @include f;
  font-size: 16px; }
a {
  color: $green;
  text-decoration: none; }
strong,
b {
  @include fs; }
button {
  background: none; }

* {
 }  // box-shadow: 0 0 0 1px red

.outer {
  overflow: hidden; }

.bold {
  @include fs; }

.center {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 50px;
  &_lg {
    max-width: 1400px; } }

.btn {
  padding: 10px 20px;
  background: $green;
  border: 2px solid transparent;
  border-radius: 22px;
  color: white;
  transition: background .2s, border-color .2s, color .2s;
  .icon {
    position: relative;
    top: 1px;
    margin-left: 10px;
    font-size: 12px;
    fill: white;
    &:first-child {
      margin: 0 10px 0 0; } }
  &_stroke {
    background: transparent;
    border-color: $green;
    color: $green;
    .icon {
      fill: $green; } }
  &_sm {
    padding: 5px 10px; } }

a.btn {
  display: inline-block; }

.lang {
  position: relative;
  width: 60px;
  &__head {
    position: relative;
    padding: 5px 20px 5px 10px;
    background: rgba(white,.8);
    border-radius: 4px;
    color: $cl;
    cursor: default;
    &:after {
      content: '';
      position: absolute;
      top: 14px;
      right: 10px;
      @include arr(8,6,$cl,b); } }
  &__drop {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    padding-top: 2px;
    opacity: 0;
    visibility: hidden;
    transition: opacity .2s, visibility .2s; }
  &__list {
    padding: 5px 0;
    background: rgba(white,.8);
    border-radius: 4px; }
  &__item {
    display: block;
    padding: 0 10px;
    color: $cl;
    transition: color .2s;
    &:hover {
      color: $green; } }
  &:hover &__head {
    &:after {
      transform: rotate(180deg); } }
  &:hover &__drop {
    opacity: 1;
    visibility: visible; } }

.header {
  position: relative;
  z-index: 20;
  &__top {
    padding: 15px 0 5px; }
  &__top &__center {
    justify-content: flex-end; }
  &__wrap {
    padding: 10px 0; }
  &__center {
    display: flex;
    align-items: center; }
  &__contact {
    display: flex;
    margin-right: 20px;
    align-items: center;
    color: white; }
  &__icon {
    margin-right: 10px;
    font-size: 0;
    .icon {
      font-size: 20px;
      fill: white;
      &-email {
        font-size: 18px; } } }
  &__socials {
    display: flex;
    padding-left: 10px;
    border-left: 2px solid white;
    align-items: center; }
  &__social {
    margin-left: 10px;
    font-size: 0;
    &:hover {
      .icon {
        fill: $green; } }
    .icon {
      font-size: 20px;
      transition: fill .2s; } }
  &__logo {
    height: 33px;
    margin-right: auto;
    background: url(../img/weatherproof-family.svg) no-repeat 0 0 / contain;
    font-size: 0;
    flex: 0 0 200px;
    transition: opacity .2s;
    &:hover {
      opacity: .7; } }
  &__nav {
    display: flex; }
  &__link {
    margin-right: 30px;
    padding: 5px 0;
    border-bottom: 2px solid transparent;
    @include fs;
    color: white;
    transition: background .2s, color .2s, border-color .2s;
    &:hover {
      border-color: $green; }
    &_border {
      padding: 5px 15px;
      border: 1px solid white;
      border-radius: 18px;
      &:hover {
        background: white;
        border-color: white;
        color: $green; } } } }

.main {
  display: flex;
  position: relative;
  height: 660px;
  margin-top: -100px;
  padding: 200px 0 150px;
  background-size: cover;
  align-items: flex-end;
  color: white;
  z-index: 2;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(#4F4E4E,.45);
    z-index: -2; }
  &__wrap {
    max-width: 500px; }
  &__theme {
    @include fb;
    text-transform: uppercase; }
  &__title {
    margin-bottom: 10px;
    @include fb;
    font-size: 40px; }
  &__text {
    margin-bottom: 30px; }
  &__btn {
    padding: 15px 30px;
    background: $green;
    border-radius: 33px;
    @include bs;
    color: white;
    line-height: 1.1; }
  &__action {
    display: block;
    @include fs;
    text-transform: uppercase; }
  &__label {
    font-size: 12px; } }

.section {
  position: relative;
  margin-top: -5vw;
  padding: calc(5vw + 50px) 0 calc(5vw + 70px);
  background: white;
  clip-path: polygon(50% 0,100% 5vw,100% 100%,0 100%,0 5vw);
  z-index: 2;
  &__row {
    display: flex;
    margin: 0 -20px;
    align-items: center; }
  &__col {
    padding: 0 20px;
    background-size: cover;
    background-position: 50%;
    flex: 0 0 50%; }
  &__theme {
    margin-bottom: 10px;
    @include fb;
    font-size: 14px;
    text-transform: uppercase; }
  &__title {
    margin-bottom: 10px;
    @include fl;
    font-size: 32px; }
  &__text {
    margin-bottom: 10px;
    a {
      @include fs; } }
  &__link {
    @include fs;
    .icon {
      position: relative;
      top: 3px;
      margin-left: 10px;
      fill: $green; } }
  &__preview {
    font-size: 0;
    text-align: center; }
  &__pic {
    max-width: 100%;
    &_mt-50 {
      margin-top: -50px; } }
  &__wrap {
    display: none; }
  &__wrap &__preview {
    margin-bottom: 30px; }
  &__inner {
    padding: 30px 0; }
  &__action {
    margin-top: 30px; }
  &__btn {
    &.active {
      .icon {
        transform: rotate(180deg); } } }
  &__bg {
    position: absolute;
    opacity: .04;
    z-index: -2; }
  &_gray {
    background: $bg; }
  &_how {
    padding: 5vw 0 calc(5vw + 100px);
    text-align: center; }
  &_how &__content {
    max-width: 640px;
    margin: 0 auto; }
  &_online {
    padding: 0;
    overflow: hidden; }
  &_online &__row {
    margin: 0;
    align-items: stretch; }
  &_online &__col {
    &:last-child {
      position: relative;
      max-width: 640px;
      padding: 120px 50px 140px;
      text-align: center; } }
  &_online &__text {
    margin-bottom: 30px; }
  &_online &__bg {
    top: 400px;
    left: calc(50% + 400px); }
  &_quality &__content {
    text-align: center; }
  &_quality &__title {
    margin-bottom: 30px; }
  &_quality &__bg {
    top: 400px;
    right: calc(50% + 400px); }
  &_gallery {
    margin: 0;
    padding: 0; }
  &_head {
    margin: 0 0 -30px;
    padding: 0;
    clip-path: none; }
  &_head &__content {
    width: 50%;
    transform: rotate(-5deg);
    text-align: center; }
  &_head &__title {
    margin: 0; }
  &_footer {
    padding-bottom: 50px;
    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 0;
      width: 51%;
      height: 2px;
      background: $border; }
    &:before {
      right: 50%;
      transform-origin: 100% 0;
      transform: rotate(-5.8deg); }
    &:after {
      left: 50%;
      transform-origin: 0 0;
      transform: rotate(5.8deg); } } }


.widget {
  display: flex;
  max-width: 960px;
  margin: 0 auto;
  padding: 30px 40px;
  background: white;
  @include bs;
  text-align: left;
  align-items: center;
  &__preview {
    margin-right: 40px;
    flex: 0 0 120px; }
  &__pic {
    width: 100%; }
  &__wrap {
    flex: 0 0 calc(100% - 160px); }
  &__title {
    margin-bottom: 15px;
    font-size: 20px;
    color: black; }
  &__text {
    margin-bottom: 25px;
    p {
      margin-bottom: 10px; } } }

.info {
  display: flex;
  margin: 0 -10px 10px;
  font-size: 14px;
  text-align: left;
  flex-wrap: wrap;
  &__item {
    display: flex;
    padding: 0 10px 20px;
    flex: 0 0 50%; }
  &__preview {
    display: flex;
    height: 40px;
    margin-right: 15px;
    align-items: center;
    justify-content: center;
    flex: 0 0 40px;
    .icon {
      font-size: 16px;
      &-materials {
        font-size: 20px; }
      &-params {
        font-size: 28px; } } }
  &__wrap {
    padding-top: 12px;
    flex: 0 0 calc(100% - 55px); }
  &__title {
    @include fs;
    color: $green;
    text-transform: uppercase; } }

.quality {
  display: flex;
  max-width: 1000px;
  margin: 0 auto;
  &__item {
    margin: 0 15px;
    padding: 20px;
    background: white;
    @include bs;
    flex: 0 0 calc(33.33% - 30px); }
  &__preview {
    display: flex;
    height: 120px;
    margin-bottom: 20px;
    align-items: center;
    justify-content: center; }
  &__title {
    margin-bottom: 20px;
    font-size: 20px; }
  &__text {
    a {
      display: inline-block; } } }

.gallery {
  display: flex;
  flex-wrap: wrap;
  &__item {
    background: black;
    flex: 0 0 25%; }
  &__pic {
    display: block;
    width: 100%;
    opacity: .8; } }

.links {
  display: flex;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: calc(50% - 1px);
    bottom: 0;
    width: 2px;
    background: $border; }
  &__item {
    display: flex;
    flex: 0 0 50%;
    &:first-child {
      padding-right: 50px; }
    &:last-child {
      padding-left: 50px; } }
  &__preview {
    display: flex;
    height: 40px;
    margin: 30px 30px 0 0;
    align-items: center;
    justify-content: center;
    flex: 0 0 40px;
    .icon {
      font-size: 37px;
      &-thumb {
        font-size: 41px; } } }
  &__wrap {
    flex: 0 0 calc(100% - 70px); }
  &__title {
    margin-bottom: 10px;
    font-size: 20px; }
  &__text {
    margin-bottom: 20px; }
  &__btn {
    border-color: $cl;
    color: $cl; } }

.contacts {
  &__item {
    display: flex;
    margin-bottom: 10px; }
  &__icon {
    display: flex;
    height: 20px;
    margin-right: 10px;
    align-items: center;
    justify-content: center;
    flex: 0 0 20px;
    .icon {
      font-size: 16px;
      fill: $green;
      &-email-fill,
      &-house {
        font-size: 14px; }
      &-location {
        font-size: 18px; } } }
  &__title {
    color: $cl;
    flex: 0 0 calc(100% - 30px); } }

.footer {
  font-size: 14px;
  &__top {
    display: flex; }
  &__section {
    &:first-child {
      padding-right: 25px;
      flex: 0 0 30%; }
    &:last-child {
      flex: 0 0 70%; } }
  &__row {
    display: flex; }
  &__col {
    padding: 0 25px;
    border-left: 1px solid $border;
    &:nth-child(1),
    &:nth-child(2) {
      flex: 0 0 20%; }
    &:nth-child(3),
    &:nth-child(4) {
      flex: 0 0 30%; } }
  &__about {
    display: flex; }
  &__map {
    margin-right: 25px;
    flex: 0 0 75px; }
  &__wrap {
    flex: 0 0 calc(100% - 100px); }
  &__logo {
    display: block;
    width: 138px;
    height: 31px;
    margin-bottom: 20px;
    background: url(../img/weatherproof-family-black.svg) no-repeat 0 0 / contain; }
  &__text {
    margin-bottom: 30px; }
  &__title {
    margin-bottom: 10px;
    @include fs;
    color: $green;
    text-transform: uppercase; }
  &__l {
    display: flex; }
  &__group {
    margin-right: 25px; }
  &__socials {
    display: flex;
    margin-right: 10px; }
  &__social {
    margin-right: 10px;
    font-size: 0;
    .icon {
      font-size: 20px;
      fill: $green; } }
  &__partner {
    margin-top: 5px;
    font-size: 0; }
  &__link {
    display: table;
    margin-bottom: 5px;
    color: $cl; }
  &__btn {
    margin-bottom: 30px; }
  &__subscribe {
    display: flex;
    margin-bottom: 10px; }
  &__input {
    padding: 0 10px;
    border: solid $border;
    border-width: 1px 0 1px 1px;
    border-radius: 5px 0 0 5px;
    flex: 0 0 calc(100% - 30px); }
  &__send {
    height: 30px;
    background: $green;
    border-radius: 0 5px 5px 0;
    font-size: 0;
    flex: 0 0 30px;
    .icon {
      font-size: 14px; } }
  &__down {
    margin-top: 30px;
    padding-top: 10px;
    border-top: 1px solid $border;
    text-align: right; }
  &__links {
    a {
      color: $cl; } } }

.field {
  &__label {
    display: flex;
    margin-bottom: 2px; }
  &__icon {
    display: flex;
    height: 20px;
    margin-right: 5px;
    align-items: center;
    justify-content: center;
    flex: 0 0 20px;
    .icon {
      font-size: 14px;
      fill: $green;
      &-user {
        font-size: 10px; }
      &-phone {
        font-size: 16px; } } }
  &__title {
    font-size: 14px;
    color: $green;
    flex: 0 0 calc(100% - 25px); }
  &__input,
  &__textarea {
    width: 100%;
    border: 1px solid $border;
    border-radius: 2px; }
  &__input {
    height: 30px;
    padding: 0 5px; }
  &__textarea {
    height: 100px;
    padding: 2px 5px; } }

.file {
  display: inline-block;
  position: relative;
  padding: 2px 7px;
  background: white;
  border: 1px solid $border;
  border-radius: 2px;
  overflow: hidden;
  &__input {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 32px;
    cursor: pointer; } }

.form {
  &__title {
    margin-bottom: 5px; }
  &__field {
    margin-bottom: 15px; }
  &__group {
    display: flex;
    margin: 0 -20px;
    flex-wrap: wrap; }
  &__group &__field {
    padding: 0 20px;
    flex: 0 0 50%; }
  &__foot {
    margin-top: 30px; } }

.tabs {
  &__nav {
    display: flex;
    margin-bottom: 20px; }
  &__btn {
    padding: 5px 10px;
    border-radius: 0;
    margin-right: -1px;
    border-width: 1px;
    flex: 1 0 auto;
    &:first-child {
      border-radius: 17px 0 0 17px; }
    &:last-child {
      border-radius: 0 17px 17px 0; }
    &.active {
      background: $green;
      color: white; } }
  &__item {
    display: none; } }

.map {
  height: 400px; }

.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 50px 20px;
  background: rgba(black,.5);
  opacity: 0;
  transition: opacity .2s;
  overflow: auto;
  z-index: 999;
  @include m {
    padding: 0; }
  &__wrap {
    position: relative;
    max-width: 660px;
    width: 100%;
    margin: auto;
    padding: 50px 60px;
    background: $bg;
    transform: scale(.9);
    transition: transform .2s;
    @include m {
      min-height: 100%;
      padding: 20px 15px;
      transform: scale(1); } }
  &__head {
    margin-bottom: 30px;
    text-align: center; }
  &__title {
    margin-bottom: 20px;
    @include fs;
    font-size: 20px; }
  &__text {
    margin-bottom: 30px; }
  &__close {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 0;
    padding: 20px;
    .icon {
      font-size: 14px; } }
  &__content {
    margin-bottom: 40px;
    p {
      margin-bottom: 10px; }
    ul {
      li {
        position: relative;
        padding-left: 20px;
        &:before {
          content: '';
          position: absolute;
          top: 6px;
          left: 0;
          @include arr(8,10,$green,r); } } } }
  &__foot {
    text-align: center; }
  &.open {
    display: flex; }
  &.visible {
    opacity: 1; }
  &.visible &__wrap {
    transform: scale(1); } }




















